.header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 14px;
    padding-bottom: 14px;
}

.logo {
    width: 34px;
    height: 22px;
}

.headerList {
    display: none;
}

.userContainer {
    display: none;
}


@media screen and (min-width: 768px) {
    .header {
        padding-top: 17px;
        padding-bottom: 17px;
    }

    .logo {
        width: 54px;
        height: 36px;
    }

    .headerList {
        display: flex;
        gap: 45px;
        align-items: center;
        margin-right: 153px;
    }

    .headerItem {
        font-family: "Montserrat Alternates", sans-serif;
        font-size: 10px;
        font-weight: 500;
    }

    .userContainer {
        display: flex;
        align-items: center;
        gap: 25px;
    }

    .button {
        color: #000;
        font-family: "Montserrat Alternates";
        font-size: 10px;
        font-weight: 500;
        padding: 8px 16px;
        border-radius: 10px;
        background: #FFB36C;
    }

    .iconUser {
        width: 40px;
        height: 40px;
        color: #556080;
    }
}

@media screen and (min-width: 1200px) {
    .header {
        padding-top: 12px;
        padding-bottom: 12px;
    }

    .logo {
        width: 82px;
        height: 56px;
    }

    .headerList {
        margin-right: 400px;
    }

    .headerItem {
        font-size: 12px;
    }

    .button {
        font-size: 12px;
        padding: 10px 20px;
    }

    .iconUser {
        width: 50px;
        height: 50px;
    }
}